o en el drenaje que no es tan bueno.")])]),_c('p',{staticClass:"texto-blanco"},[_c('em',[_vm._v("Estas arterias y venas son buenas cuando se debe aplicar la preservación localizada, pero no cuando se aplican grandes volúmenes de solución preservante que requieran grandes volúmenes de drenaje. Se debe direccionar hacia el corazón para que irrigue todo el cuerpo. Si se seleccionan se debe irrigar hacia el lado contrario para que irrigue la extremidad de la mano; sin embargo, con el solo direccionamiento hacia el corazón la mano queda con una irrigación completa, cuyo factor además hace que se deshidrate y fije la piel del brazo hasta el punto de verse poco estético y completamente rígido; por presentar tantas ramificaciones suele tener problemas de drenaje por la sobrecarga de fluido en la extremidad; el diámetro de la arteria y de la vena son de diámetros muy pequeños, para localizarla se debe extender el brazo.")])])])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col m-0 fnegro order-lg-first order-last contenedorNegro"},[_c('div',{staticClass:"p-5"},[_c('figure',[_c('img',{staticClass:"mb-4",attrs:{"src":require("@/assets/curso/t5/f19.svg"),"alt":"imagen relacionada"}})]),_c('p',{staticClass:"texto-blanco"},[_c('em',[_vm._v("Es el segundo punto de inyección y drenaje más recurrente; es la continuación de las arterias ilíacas. Al ubicarse la arteria hacia al corazón irriga muy bien todo el cuerpo. Se debe dirigir también a la pierna para irrigar, el diámetro de ambas es grande y la incisión queda oculta, son relativamente fáciles de encontrar; sin embargo, en casos de cuerpos obesos se complica un poco, a pesar de no ser tan cercano al corazón tiene muy buenos resultados.")])])])]),_c('div',{staticClass:"col-lg-7 m-0 order-first order-lg-last"},[_c('figure',[_c('img',{staticClass:"mb-4",attrs:{"src":require("@/assets/curso/t5/f21.png"),"alt":"imagen relacionada"}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;retur